.Works{
    position: relative;
    z-index: 100;
}

.works_wrapper{

}
.works_title__wrapper{
    margin-top: 100px;
    margin-bottom: 50px;

    display: flex;
    flex-direction: row;
    justify-content: space-between;

    align-items: center;    

}
.works_title{
    font-size: 32px;
    font-weight: 500;
}
.works_span{
    color: var(--textColorSecond);
}
.works_title_line{
    width: 70%;
    height: 3px;
    border-radius: 5px;
    background-color: var(--textColorSecond);
}


.works_content__wrapper{
    display: flex;
    flex-direction: column;
    gap: 40px;
}
.worksBiG_wrapper{
    column-count: 2;
}
.worksSmall_wrapper{
    column-count: 4;
}

.works_footer{
    margin-top: 20px;

    display: flex;
    flex-direction: row;
    gap: 30px;
}

.works_decor__svgDoubleSquareSvg{
    position: absolute;
    top: 500px;
    left: -375px;

    width: 600px;
    height: 600px;   
    z-index: -10;

    fill: #c1f285;
    filter: blur(5px) drop-shadow(0px 0px 60px #c1f285);
}


.works_decor__figures__container{
    position: relative;
}
.works_decor__imgCircles{
    position: absolute;
    top: 800px;
    right: -300px;

    width: 500px;
    height: 500px;

    transform: rotate(90deg);

    filter: invert(40%);
    z-index: -10;

    filter: invert(40%) blur(1.5px);
}


/* MEDIA */
@media (max-width: 1100px){
    .worksSmall_wrapper{
        column-count: 2;
    }
}
@media (max-width: 750px){
    .works_title__wrapper{
        margin-top: 50px;
    }
    .works_title_line{
        width: 50%;
    }
}
@media (max-width: 600px){
    .works_title{
        font-size: 26px;
    }
}
@media (max-width: 500px){
    .works_title{
        font-size: 26px;
    }
    .worksBiG_wrapper{
        column-count: 1;
    }
}
@media (max-width: 400px){
    .works_title_line{
        width: 30%;
    }
    .worksSmall_wrapper {
        column-count: 1;
    }
}